export default {
	data() {
		return {
			name:"",
			password:"",
			vercode:"",
			iftrue:false,
			ifaleat:false,
			ifaleat_1:false,
			ifaleat_2:false,
			iflase:true
		}
	},
	onLoad() {
			
	},
	methods: {
		gohome:function(){
				// if(this.name==""){
				// 	console.log(1)
				// }
				uni.request({
				    url: 'http://139.159.158.208/api/v1/index',
					data: {
					   name:this.name,
					   password:this.password,
					   vercode:this.vercode
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded', 
					},
					method:"POST",
					sslVerify:false,
				    success: (res) => {
				        console.log(res.data);
						if(res.data.code==401){
							this.iflase=false
							this.iftrue=true
							this.ifaleat_1=true
						}else if(res.data.code==10002){
							this.iflase=false
							this.iftrue=true
							this.ifaleat=true
						}else if(res.data.code==30001){
							this.iflase=false
							this.iftrue=true
							this.ifaleat_2=true
						}else if(res.data.code==200){
							uni.switchTab({
								url: '/pages/home/index'
							})
						}
						
				    },
				}); 
		},
		goregister:function(){
			uni.navigateTo({
				url: '/pages/login/register/index',
			})
		}
	}
}
